@import url(https://fonts.googleapis.com/css2?family=MedievalSharp&display=swap);
*{margin:0;padding:0;font-size:180%}html,body{height:100vh;background-image:url("https://c.pxhere.com/images/6e/62/14ec0e80e4a8bfd7510e8745c88f-1628448.jpg!d");background-size:cover;background-position:center;background-repeat:repeat}.App{font-family:MedievalSharp,cursive;text-shadow:2px 0 0 #fff, -2px 0 0 #fff, 0 2px 0 #fff, 0 -2px 0 #fff, 1px 1px #fff, -1px -1px 0 #fff, 1px -1px 0 #fff, -1px 1px 0 #fff;text-align:center;margin:0;padding:0;width:100%;height:100vh;background-size:cover;background-position:center;background-repeat:repeat}.control{position:center;height:50vh;cursor:pointer;display:flex;flex-flow:row wrap;align-items:left}.control:hover .circle-click{background-image:url(/project_lt2216/static/media/wallet.4eeaea61.png);background-size:55%;background-repeat:no-repeat;background-position:center}.control .prompt img{height:50vh;padding:10px 25px;text-align:left}.control .status,.control .status-talk{bottom:0;padding-left:50px;padding-right:20px;width:100vw;font-family:MedievalSharp, cursive;font-style:italic;font-weight:light;font-size:20pt;text-align:center;color:rgba(99,61,11,0.788);z-index:1}.control .status .status-text,.control .status-talk .status-text{z-index:5;position:relative;white-space:normal}.control .circle,.control .circle-click,.control .circle-speaking,.control .circle-recognising{position:center;width:80px;height:80px;left:-40px;border-radius:50px;cursor:pointer;border:0;background-color:#a5750e;z-index:0}.control .circle-click{border:0px;background-color:#fff}.control .circle-speaking{border:0px;background-color:#fff;-webkit-animation:bordering 2s infinite;animation:bordering 2s infinite}.control .circle-recognising{-webkit-animation:bordersize 2s infinite;animation:bordersize 2s infinite}.select-wrapper{margin-left:40px;margin-right:20px;top:35vh;position:absolute}.select{list-style-type:none;padding:0;margin:0;display:flex;flex-flow:row wrap;align-items:space-around;justify-content:flex-start}.flex{margin:0.5em;padding:1em;flex-basis:20vw;cursor:pointer;border-radius:5px;background:#fff;border:2px solid #724707}.flex:hover{background:rgba(99,61,11,0.788)}img{object-fit:contain;width:100%;border-radius:5px}@-webkit-keyframes glowing{0%{background-position:0 0}50%{background-position:400% 0}100%{background-position:0 0}}@keyframes glowing{0%{background-position:0 0}50%{background-position:400% 0}100%{background-position:0 0}}@-webkit-keyframes bordering{0%{box-shadow:0 0 0px rgba(222,255,138,0.5)}50%{box-shadow:0 0 0px 8vh rgba(222,255,138,0.9)}100%{box-shadow:0 0 0px rgba(222,255,138,0.5)}}@keyframes bordering{0%{box-shadow:0 0 0px rgba(222,255,138,0.5)}50%{box-shadow:0 0 0px 8vh rgba(222,255,138,0.9)}100%{box-shadow:0 0 0px rgba(222,255,138,0.5)}}@-webkit-keyframes bordersize{0%{box-shadow:0 0 0px rgba(255,255,255,0.5)}50%{box-shadow:0 0 0px 8vh rgba(255,255,255,0.9)}100%{box-shadow:0 0 0px rgba(255,255,255,0.5)}}@keyframes bordersize{0%{box-shadow:0 0 0px rgba(255,255,255,0.5)}50%{box-shadow:0 0 0px 8vh rgba(255,255,255,0.9)}100%{box-shadow:0 0 0px rgba(255,255,255,0.5)}}[data-xstate]{justify-self:stretch;align-self:stretch;border:none}

